___CSS_LOADER_EXPORT___.push([module.id, `.modal-add-project, #modal-edit-project{
    height: 500px;
    width: 300px;
    border-radius: 20px;
    margin: 100px auto auto auto;
}
.modal-add-project>form, #modal-edit-project>form{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
    gap: 15px;
}
.close-modal{
    margin-bottom: 30px;
    margin-left: auto;
    height: 25px;
    width: 25px;
    border-radius: 100%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    cursor: pointer;
}
.modal-add-project>form div, #modal-edit-project>form div{
    width: 100%;
}
#project-name, #edit-project-name{
    width: 100%;
    height: 25px;
}
#project-desc, #edit-project-desc{
    width: 100%;
    height: 200px;
    resize: none;
}
#project-name, #project-desc, #edit-project-name, #edit-project-desc{
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #514e4e;
}
.modal-add-project>form label, #modal-edit-project>form label{
    font-weight: 700;
}




.modal-add-todo{
    height: 320px;
    width: 700px;
    padding: 20px 15px;
    margin: 200px auto auto auto;
    border-radius: 20px;
}
.modal-add-todo>form{
    height: 100%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.modal-btn-add{
    margin-top: auto;
    padding: 5px;
    cursor: pointer;
}

#close-todo-modal{
    display: block;
    margin-bottom: 0;
}
.modal-add-todo>form>div:first-child{
    flex-basis: 100%;
    height: 0px;
    margin-bottom: 20px;
}
.modal-add-todo>form>div:nth-child(4){
    flex-basis: 100%;
}
.modal-add-todo>form>fieldset{
    flex-basis: 100%;
    padding: 5px;
}
.modal-add-todo input[type = 'text']{
    padding: 5px ;
    font-size: 1rem;
}
#todo-note{
    width: 100%;
}
#todo-name{
    width: 300px;
}
.modal-add-todo>form>div:nth-child(2){
    margin-right: auto;
}
#modal-btn-add-todo{
    margin-top: 10px;
}

#low{
    accent-color: green;
}
#medium{
    accent-color: yellow;
}
#high{
    accent-color: #c46a59;
}

#confirm-delete{
    padding: 10px;
    margin-left: auto;
    border-radius: 15px;
}
#confirm-delete h3{
    margin-bottom: 10px;
}
#confirm-delete button{
    padding: 2px 5px;
}`, "",{"version":3,"sources":["webpack://./src/modal.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,4BAA4B;AAChC;AACA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,uBAAuB;IACvB,aAAa;IACb,SAAS;AACb;AACA;IACI,mBAAmB;IACnB,iBAAiB;IACjB,YAAY;IACZ,WAAW;IACX,mBAAmB;IACnB,yDAA8C;IAC9C,eAAe;AACnB;AACA;IACI,WAAW;AACf;AACA;IACI,WAAW;IACX,YAAY;AAChB;AACA;IACI,WAAW;IACX,aAAa;IACb,YAAY;AAChB;AACA;IACI,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;AAC7B;AACA;IACI,gBAAgB;AACpB;;;;;AAKA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,4BAA4B;IAC5B,mBAAmB;AACvB;AACA;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,eAAe;IACf,SAAS;AACb;AACA;IACI,gBAAgB;IAChB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,gBAAgB;AACpB;AACA;IACI,gBAAgB;IAChB,WAAW;IACX,mBAAmB;AACvB;AACA;IACI,gBAAgB;AACpB;AACA;IACI,gBAAgB;IAChB,YAAY;AAChB;AACA;IACI,aAAa;IACb,eAAe;AACnB;AACA;IACI,WAAW;AACf;AACA;IACI,YAAY;AAChB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;AACA;IACI,oBAAoB;AACxB;AACA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,mBAAmB;AACvB;AACA;IACI,mBAAmB;AACvB;AACA;IACI,gBAAgB;AACpB","sourcesContent":[".modal-add-project, #modal-edit-project{\n    height: 500px;\n    width: 300px;\n    border-radius: 20px;\n    margin: 100px auto auto auto;\n}\n.modal-add-project>form, #modal-edit-project>form{\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: flex-start;\n    padding: 20px;\n    gap: 15px;\n}\n.close-modal{\n    margin-bottom: 30px;\n    margin-left: auto;\n    height: 25px;\n    width: 25px;\n    border-radius: 100%;\n    background-image: url(./asset/icons/close.svg);\n    cursor: pointer;\n}\n.modal-add-project>form div, #modal-edit-project>form div{\n    width: 100%;\n}\n#project-name, #edit-project-name{\n    width: 100%;\n    height: 25px;\n}\n#project-desc, #edit-project-desc{\n    width: 100%;\n    height: 200px;\n    resize: none;\n}\n#project-name, #project-desc, #edit-project-name, #edit-project-desc{\n    paddi