*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
:root{
    --primary : #fff8f6;
    --secondary : #FFE8E1;
    --tertiary : #514e4e;
    --color-four : #d9d6d6;
    --low : #a5bb62;
    --medium : #e3da2e;
    --high : #c46a59;
}
body{
    display: grid;
    height: 100vh;
    grid-template: 1fr / auto 1fr;
}
.sidebar{
    display: flex;
    flex-direction: column;
    gap: 50px;
    padding: 0 20px;
    background-color: var(--primary);
    border-right: 1px solid var(--tertiary);
}
.sidebar>header{
    display: flex;
    align-items: center;
    height: 80px;
    font-size: 2.5rem;
    font-weight: 900;
    font-family: pattaya;
}
.project-container{
    display: flex;
    flex-direction: column;
    gap: 6px;
}
.project-container-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}
.project-container-title>div:first-child{
    font-size: 1.3rem;
    font-weight: 600;
}
.add-project{
    min-width: 25px;
    min-height: 25px;
    border-radius: 100%;
    cursor: pointer;
    border: 2px solid var(--tertiary);
    background-color: white;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}
.project{
    text-align: left;
    padding: 5px 10px;
    border-radius: 8px;
    background-color: white;
    border: 0.5px solid var(--tertiary);
    color: var(--tertiary);
    cursor: pointer;
}
.project:hover, .add-project:hover{
    background-color: var(--secondary);
    border: none;
    font-weight: 800;
    outline: 2px solid var(--tertiary);
}
.add-project:active{
    background-color: var(--primary);
    outline: 0.5px solid var(--tertiary);
}
.project:active{
    border: none;
    outline: 0.5px solid var(--tertiary);
}

.content{
    display: flex;
    flex-direction: column;
    padding: 20px 40px;
}
.project-controls{
    height: 30px;
    text-align: right;
}
.project-controls button{
    height: 30px;
    width: 30px;
    border-radius: 100%;
}
.project-info{
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 5px;
}
.project-info>header{
    font-size: 3rem;
}
.project-info>div{
    color: var(--tertiary);
    padding-left: 2px;
    overflow-y: auto;
}
.delete-project{
    margin-left: 10px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}
.edit-project{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}
.delete-project, .edit-project{
    border: none;
    background-color: white;
    cursor: pointer;
}
.delete-project:active{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}
.edit-project:active{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.todo-card{
    height: 70px;
    border-top: 1px solid var(--color-four);
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2px;
    margin-left: 10px;
}
.top-row, .bottom-row{
    display: flex;
    gap: 8px;
    align-items: center;
}
.bottom-row{
    padding-left: 44px;
    font-size: 0.9rem;
    color: var(--tertiary);
}
.top-row button{
    height: 25px;
    width: 25px;
    border-radius: 100%;
    cursor: pointer;
    background-color: white;
}
.list-done{
    margin-right: 10px;
    border: 2px solid var(--tertiary);
}
.list-title{
    font-size: 1.3rem;
    font-weight: 600;
    line-height: 100%;
}
.delete-project:hover, .edit-project:hover{
    border: 0.5px solid lightgray;
}

.list-add{
    height: 60px;
    display: inline-flex;
    align-items: center;
    padding: 0 20px 0 0;
    gap: 18px;
    cursor: pointer;
    color: var(--tertiary);
    user-select: none;
    border-top: 1px solid var(--color-four);
